*{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

table {
    border: 2px solid forestgreen;
    width: 350px;
    margin-top: 10px;
    border-radius: 10px;
  }
    
  th {
    border-bottom: 1px solid black;
  }
    
  td {
    text-align: center;
  }

  .delete-btn{
    color: red;
    font-weight: bolder;
  }

  .edit-btn{
    color: green;
    font-weight: bolder;
  }