*{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: #fff;
}

.heading{
    margin-top: 30px;
    padding: 0;
    text-align: center;
}

.main__container{
    display: flex;
    width: 100vw;
    height: 100vh-30px;
    justify-content: space-evenly;
    align-items: center;
}

img{
    width: 200px;
}

.right__container{
    border: black solid 2px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.register__form{
    display: flex;
    flex-direction: column;
}

input{
    border: black solid 2px;
    width: 200px;
    padding: 5px;
    font-size: medium;
    margin: 3px;
    border-radius: 20px;
}

.register__btn{
    margin-top: 10px;
    background-color: #90EE90;
    font-size: medium;
    border: none;
    width: 120px;
    height: 25px;
    align-self: center;
    border-radius: 10px;
    color: black;
}

span{
    color: red;
}

@media (max-width:500px) {
    .main__container{
      flex-direction: column;
    }
 }